.skillSet{
    margin-top: 2rem !important;
    margin-bottom: 3rem;
  }
  .skill-head{
    color:  rgb(33, 37, 41);
    text-align: left;
    border-bottom: 1px solid rgb(33, 37, 41);
  }
  .skill-head span img{
    width: 3rem;
    margin-right: 1rem;
    padding-bottom: 1rem;
  }
  .skill{
    margin: 2rem 3rem 2rem 0;
    background-color: rgb(33, 37, 41);
    box-shadow:
    0 0.6px 2px rgba(0, 0, 0, 0.112),
    0 1.5px 5px rgba(0, 0, 0, 0.162),
    0 2.9px 9.8px rgba(0, 0, 0, 0.2),
    0 5.1px 18px rgba(0, 0, 0, 0.238),
    0 9.6px 34px rgba(0, 0, 0, 0.288),
    0 23px 80px rgba(0, 0, 0, 0.4)
  ;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    border-radius: 20px;
    text-align: center;
  
  }
  
 
  
  .square-box{
    position: relative;
    width: 100%;
    overflow: hidden;
  }
  .square-box:before{
    content: "";
    display: block;
    padding-top: 100%;
  }
  .square-content{
    position:  absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    color: white;
  }
  .square-content div {
   display: table;
   width: 100%;
   height: 100%;
  }
  .square-content span {
    display: table-cell;
    text-align: center;
    vertical-align: middle;
    color: white
  }
  .square-content span img{
    height: 4rem;
    margin-bottom: 1rem;
  }

  .skillSet .skill:last-of-type{
    margin-right: 0rem;
  }